          page: 1,
          loading: false,
          perPage: 5
        }
      },
      mounted() {
        this.fetchData()
      },

      methods: {
          async fetchData() {
            const res = await fetch (lstProysJson);
            const val= await res.json();
            this.proyectos=val;
            console.log(val);
          }
      }
    }
  </script>
  